import {combineReducers} from "redux";

import {TableReduce} from "../Reducer/table";
import {ExamReducer} from "../Reducer/exam";
import {ConfigReducer} from "../Reducer/config";
import {ProblemReducer} from "../Reducer/problem";
import {UserReducer} from "../Reducer/user";
import {ManageReducer} from "../Reducer/manage";
import {SubmissionReducer} from "../Reducer/submission";
import {ContestReducer} from "../Reducer/contest";
import {ProblemSetReducer} from "../Reducer/problemSet";
import {CommonReducer} from "../Reducer/common";

//  reducer 
export const rootReducers =
    combineReducers({
        TableReduce,            // 
        ExamReducer,            // 
        ConfigReducer,          // 
        ProblemReducer,         // 
        UserReducer,            // 
        ManageReducer,          // 
        SubmissionReducer,
        ContestReducer,
        ProblemSetReducer,
        CommonReducer
    })