import {Role} from "../Type/Iuser";


/**
 * 
 * @param roles 
 * @param minRole 
 */
export default function judgeAuth(roles: Role[] | undefined, minRole: Role[]): boolean {
    if (roles === undefined) return false
    let ok = false;
    roles.map(v => {
        if (minRole.includes(v)) ok = true;
        return undefined
    })
    return ok;
}
